import { ApolloClient } from 'apollo-client'
import { createHttpLink } from 'apollo-link-http'
import { InMemoryCache } from 'apollo-cache-inmemory'
import VueApollo from 'vue-apollo';
//  API  HTTP 
const httpLink = createHttpLink({
  // 
  uri: 'https://api.studio.thegraph.com/query/1315/artinvest/1.0.3',
  // headers: {
  //   // searchToken: '21222'
  //   'accept-Language': "en-GB"
  // }
})

const httpLinkToken = createHttpLink({
  // 
  uri: 'https://api.studio.thegraph.com/query/1315/art/1.0.4',
  // headers: {
  //   // searchToken: '21222'
  //   'accept-Language': "en-GB"
  // }
})

// 
const cache = new InMemoryCache()

//  apollo 
const apolloClient = new ApolloClient({
  link: httpLink,
  cache,
})
const apolloClientToken = new ApolloClient({
  link: httpLinkToken,
  cache,
})
const apolloProvider = new VueApollo({
  defaultClient: apolloClient,
  clients: {
    defaultClient: apolloClient,
    tokenClient: apolloClientToken
  }
})
export default apolloProvider;